import React from "react";
import Home from "./Containers/Home";
import { BrowserRouter as Router, Route } from "react-router-dom"
import AboutUs from "./Containers/AboutUs";
import ContactUs from "./Containers/ContactUs";
import Films from "./Containers/Films";
import News from "./Containers/News";
import Services from "./Containers/Services";
function App() {
  return (
    <div style={{ height: "100%" }}>
      <Router>
        <Route exact path="/thoughtfullclown/" component={Home} />
        <Route exact path="/thoughtfullclown/films" component={Films} />
        <Route exact path="/thoughtfullclown/news" component={News} />
        <Route exact path="/thoughtfullclown/services" component={Services} />
        <Route exact path="/thoughtfullclown/about_us" component={AboutUs} />
        <Route exact path="/thoughtfullclown/contact_us" component={ContactUs} />
      </Router>
    </div>
  );
}

export default App;
